<template>
    <div class="headerDiv">
        <div class="divLogo">
            <img class="logoImg" alt="Vue logo" src="~@/assets/logo/mark.svg" />
            <span class="title01">Blockchain forum</span>

            <div class="searchInputDiv">
                <input class="input01" type="text" placeholder="搜尋" />
                <router-link class="searchIcon" to="/site_search">
                    <img alt="Vue logo" src="~@/assets/icon/search-ondark.svg" />
                </router-link>
            </div>
        </div>

        <div class="userInfoDiv" v-if="loginStatus">
            <img alt="" src="~@/assets/icon/user.svg" />
            <span class="span02">林雅婷</span>
            <img
                :style="{cursor: 'pointer'}"
                alt=""
                src="~@/assets/icon/chevron-down-ondark.svg"
                v-on:click="setIsAccountMenu(true)"
                ref="imgDown"
            />
        </div>

        <div class="userInfoDiv" v-if="!loginStatus">
            <img alt="" src="~@/assets/icon/user.svg" />
            <router-link class="span01" to="/login" v-on:click.native="setSelectMenuId(0)">登入/註冊</router-link>
        </div>
        <AccountMenu />
    </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import AccountMenu from "./AccountMenu.vue";

export default {
    name: "Header",
    props: {
        msg: String
    },
    components: {
        AccountMenu
    },
    computed: mapGetters({
        loginStatus: "getLoginStatus"
    }),
    methods: {
        ...mapActions(["setSelectMenuId", "setIsAccountMenu"]),
        docClick: function(e) {
            if (this.$refs.imgDown) {
                if (!this.$refs.imgDown.contains(e.target) && this.loginStatus)
                    this.setIsAccountMenu(false);
            }
        }
    },
    created() {
        document.addEventListener("click", this.docClick);
    },
    destroyed() {
        document.removeEventListener("click", this.docClick);
    }
};
</script>

